.cart {
  width: 100%;
  padding-bottom: 5rem; }
  .cart__icons, .cart__remove {
    -webkit-tap-highlight-color: transparent;
    background: transparent;
    border: none;
    box-shadow: none;
    cursor: pointer;
    outline: none;
    position: relative; }
  .cart__remove {
    font-size: 1.2rem;
    color: #cf3838; }
  .cart__icons {
    line-height: 0;
    font-weight: 600;
    padding: .2rem;
    border: 0.1rem solid #212529; }
    .cart__icons:hover {
      background-color: #212529;
      color: #fff;
      border-color: #212529; }
  .cart__empty {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 0;
    margin: 0 auto;
    width: 100%;
    color: #a7a7a7;
    transform: translate3d(0, -50%, 0); }
  .cart__subtotal {
    backface-visibility: hidden;
    background: #212529;
    bottom: 0;
    left: 0;
    box-shadow: 0 -0.2rem 2rem 0 rgba(0, 0, 0, 0.08);
    color: #fff;
    display: inline-block;
    padding: 1rem 1.6rem;
    position: fixed;
    text-align: center;
    width: 100%; }
