.app {
  min-height: 100vh;
  position: relative;
  width: 100%;
  padding-top: 5.5rem; }
  .app__container {
    display: block;
    margin: 0 auto;
    padding-left: 1.6rem;
    padding-right: 1.6rem;
    position: relative;
    width: 100%; }
    @media screen and (min-width: 1279px) {
      .app__container {
        width: 1279px; } }
  .app.app--is-drawer-visible {
    overflow-y: hidden;
    transition: opacity 225ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1); }
    .app.app--is-drawer-visible:before {
      backface-visibility: visible;
      background: radial-gradient(ellipse at center, #7a7a7a 0, #000 100%);
      bottom: 0;
      content: "";
      height: 100vh;
      opacity: .75;
      pointer-events: all;
      position: fixed;
      right: 0;
      visibility: visible;
      width: 100vw;
      z-index: 15; }
