.single-product {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: left;
  margin: 0 auto;
  position: relative;
  width: 100%;
  padding-bottom: 6.5rem; }
  @media screen and (min-width: 1279px) {
    .single-product {
      width: 1279px;
      padding-top: 1.6rem;
      padding-left: 1.6rem;
      padding-right: 1.6rem; } }
  .single-product .product__image, .single-product .product__content {
    width: 100%; }
    @media screen and (min-width: 1279px) {
      .single-product .product__image, .single-product .product__content {
        width: calc(100% / 2); } }
  .single-product .badge {
    font-size: 1.5rem;
    position: absolute;
    left: 1.6rem;
    bottom: 0;
    margin: 0; }
    @media screen and (min-width: 1279px) {
      .single-product .badge {
        right: 0;
        top: 0;
        left: auto;
        bottom: auto;
        margin: 0; } }
