.header {
  backface-visibility: hidden;
  background-color: #fff;
  height: 5.5rem;
  left: 0;
  position: fixed;
  top: 0;
  transform: translate3d(0, 0, 0);
  width: 100%;
  z-index: 10;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.02); }
  .header__default, .header__context {
    align-items: center;
    display: flex;
    padding-bottom: 1.25rem;
    padding-top: 1.25rem; }
  .header__default {
    justify-content: space-between;
    padding-bottom: 1.25rem;
    padding-top: 1.25rem; }
  .header__context {
    justify-content: flex-start;
    padding-bottom: 1.25rem;
    padding-top: 1.25rem; }
  .header__default {
