.product {
  width: 100%; }
  .product__image {
    overflow: hidden;
    width: 100%;
    position: relative; }
    .product__image img {
      width: 100%; }
  .product__name {
    font-size: 1.3rem;
    font-weight: 600;
    margin-top: 1rem;
    margin-bottom: .5rem;
    overflow: hidden;
    text-align: center;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%; }
  .product__pricing {
    display: flex;
    justify-content: center;
    align-items: center; }
  .product__price {
    font-size: 1.4rem; }
    .product__price--from {
      margin-right: 1rem;
      font-size: 1.2rem;
      color: #a7a7a7;
      text-decoration: line-through; }
  .product__sizes {
    display: block; }
    .product__sizes__title {
      color: #a7a7a7;
      font-size: 1.4rem;
      margin-bottom: 1rem; }
    .product__sizes__warning {
      color: #cf3838;
      font-size: 1.3rem;
      margin-bottom: 2rem; }
  .product__btn-group {
    display: flex;
    justify-content: left;
    align-items: center; }
  .product__filter {
    position: relative;
    cursor: pointer;
    font-weight: 600;
    padding: .8rem 1rem;
    border: .1rem solid #dfdfdf;
    margin-right: .8rem;
    background: transparent;
    outline: none;
    border-radius: .5rem; }
    .product__filter--selected {
      background-color: #212529;
      color: #fff;
      border-color: #212529; }
  .product__content {
    padding-left: 1.6rem;
    padding-right: 1.6rem; }
    .product__content .product__name {
      text-align: left;
      font-size: 2rem;
      margin-bottom: .8rem;
      letter-spacing: -0.05rem; }
    .product__content .product__pricing {
      justify-content: left; }
    .product__content .product__price {
      font-size: 1.6rem;
      margin-bottom: .8rem;
      font-weight: 600; }
      .product__content .product__price--installments {
        font-size: 1.4rem;
        margin-left: 1rem;
        color: #a7a7a7; }
  .product__actions {
    backface-visibility: hidden;
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 1rem 1.6rem;
    width: 100%;
    z-index: 10; }
    @media screen and (min-width: 1279px) {
      .product__actions {
        position: relative;
        z-index: 0;
        padding: 2rem 0;
        width: auto; } }
  .product__add-to-cart {
    background-color: #212529;
    border-color: #212529;
    border-radius: .5rem;
    border: none;
    box-shadow: 0 0.2rem 2rem 0 rgba(0, 0, 0, 0.5);
    color: #fff;
    display: inline-block;
    line-height: 1;
    outline: none;
    cursor: pointer;
    padding: 1.2rem;
    position: relative;
    text-align: center;
    transform: translate3d(0, 0, 0);
    width: 100%; }
    @media screen and (min-width: 1279px) {
      .product__add-to-cart {
        width: 50%; } }
  .product__list {
    width: 100%;
    padding-left: 1.6rem;
    padding-right: 1.6rem; }
    .product__list .product__image {
      width: 25%; }
    .product__list__item {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      padding-top: 1rem;
      padding-bottom: 1rem;
      box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.08); }
    .product__list__row {
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start; }
    .product__list__info {
      width: 50%;
      padding-left: 1rem;
      padding-right: 1rem; }
    .product__list__name {
      font-size: 1.2rem;
      font-weight: 600;
      margin-top: 0;
      margin-bottom: .5rem; }
    .product__list__size {
      font-size: 1.2rem;
      color: #a7a7a7; }
    .product__list__quantity {
      display: flex;
      flex-direction: row;
      align-items: center;
      display: flex; }
    .product__list__input {
      padding-left: 1.6rem;
      padding-right: 1.6rem; }
    .product__list__pricing {
      display: flex;
      flex-direction: column;
      text-align: right;
      width: 25%; }
    .product__list__current {
      font-size: 1.4rem;
      font-weight: 600;
      margin-bottom: 1rem; }
    .product__list__installments {
      font-size: 1.2rem;
      color: #a7a7a7; }
